.sticky {
	display: block;
}

#page {
	position: relative;
	min-height: 100%;
}

#content {
	@include prefix( transition, $trans_25, o moz webkit );
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

section {
	margin: 30px auto;

	&.error-404 {
		max-width: 1180px;
	}
}

#searchform.wonka-form-inline {
	margin: 15px auto;

	.wonka-btn {
		margin-left: -2px;
	}
}

/*=====================================
=            Search styles            =
=====================================*/
/* The overlay effect with black background */
.overlay {
	height: 100%;
	width: 100%;
	display: none;
	position: fixed;
	z-index: 99999;
	top: 0;
	left: 100%;
	opacity: 0;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0, 0.9); /* Black with a little bit see-through */
	@include prefix( transition, $trans_25, o moz webkit );
}

/* The content */
.overlay-content {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	top: 46%;
	width: 80%;
	text-align: center;
	margin-top: 30px;
	margin: auto;
	@include prefix( transition, $trans_25, o moz webkit );

	form#searchform {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;

		/* Style the search field */
		input[type=text] {
			width: 100%;
			padding: 13px;
			@include font-size( 1.1 );
			@include prefix( transition, $trans_25, o moz webkit );
		}

		input[type=text]:hover {
			background: #f1f1f1;
		}

		button {
			width: 100%;
			margin-top: -2px;
			margin-left: 0;
			@include prefix( transition, $trans_25, o moz webkit );
		}

		@include media-breakpoint-up( md ) {
			width: 65%;

			input[type=text] {
					width: 50%;
				}
			button {
						width: auto;
						margin-top: 0;
						margin-left: -2px;
					}
		}
	}
}

/* Close button */
.overlay .closebtn {
	position: absolute;
	top: 20px;
	right: 45px;
	font-size: 60px;
	cursor: pointer;
	color: white;
	@include prefix( transition, $trans_25, o moz webkit );
}

.overlay .closebtn:hover {
	color: #ccc;
}
/*=====  End of Search styles  ======*/
